import React, { useContext } from "react";
import Container from "react-bootstrap/Container";
import Jumbotron from "react-bootstrap/Jumbotron";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import { UserContext } from "./Context";
import { azureUserAgent } from "./Fire";


export default () => {
    const [,setUser] = useContext(UserContext);

    const changeAuthState = event => {
        event.preventDefault();
        const request = { scopes: ["user.read"] };
        
        azureUserAgent.loginPopup(request).then(function (loginResponse) {
            setUser(loginResponse);
        }).catch(function (error) {
            console.log(error);
        });
    }

    return <React.Fragment>
        <Jumbotron className="mb-5">
            <Container>
                <h1 className="display-4">SUNRXS</h1>
                <p className="lead">Report Management Tool</p>
                <hr className="my-4"/>
                <p>Use the below form to login into your account.</p>
            </Container>
        </Jumbotron>

        <Container>
            <Form onSubmit={changeAuthState}>
                <Button type="submit" variant="primary" size="sm" className="mt-5 mb-5">Sign in with your Office login</Button>
            </Form>
        </Container>
    </React.Fragment>
}